<template>
  <div class="address">
    <van-nav-bar
      title="选择地址"
      left-text="返回"
      left-arrow
      fixed
      @click-left="back"
    />

    <van-address-list
        :list="list"
        default-tag-text="默认"
        :switchable="false"
        @add="add"
        @edit="edit"
    />
    <!-- list地址列表  @edit 编辑   @add添加-->
  </div>
</template>

<script>
export default {
  name: "Address",
  data(){
      return{
          chosenAddressId: '1',
          list: []
      }
  },
  created(){
      this.getUserAddressData();
  },
  methods: {
    // 返回上一级
    back() {
      this.$router.go(-1);
    },
    // 新增地址
    add(){
        this.$router.push({name: "NewAddress"})
    },
    // 编辑地址
    edit(item){
        console.log("传入的每一项数据",item);
        this.$router.push({name: "NewAddress" , params:{aid: item.aid}});
    },
    // 获取用户地址数据
    getUserAddressData(){
        //获取token
        let tokenString = this.$cookies.get('TOKEN');
        this.axios({
            method: "GET",
            url: "/findAddress",
            params: {
                tokenString
            }
        })
        .then((res) => {
        
        

        if (res.data.code == 700) {
        //用户未登录, 拦截操作, 通知用户去登录
            this.$toast('请先登录');
            setTimeout(() => {
                this.$router.push({name: 'Login'});
            }, 2000)

            return;
        }

        if (res.data.code === 20000) {
            // 遍历数据
            // forEach ==>驼峰法命名
            res.data.result.forEach(v =>{
                // 修改数据
                v.isDefault = Boolean(v.isDefault);
                v.address = `${v.province}${v.city}${v.county}${v.addressDetail}`;
            })
            this.list = res.data.result;
            console.log(this.list);

        }

        })
        .catch((err) => {
        
        });
    }
  },
};
</script>

<style lang="less" scoped>
    .address {
    padding-top: 46px;
    /deep/ .van-nav-bar .van-icon {
        color: #eb2403;
    }

    /deep/ .van-nav-bar__text {
        color: #eb2403;
    }
    /deep/ .van-address-item .van-radio__icon--checked .van-icon{
        border-color: #eb2403;
        background-color: #eb2403;
    }
    /deep/ .van-tag--danger{
        background-color: #eb2403;
    }
        /deep/ .van-button--danger{
            border-color: #eb2403;
            background-color: #eb2403;
        }

    }
</style>